@charset "UTF-8";
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/pt-sans-bold.woff2") format("woff2"), url("../fonts/pt-sans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Rostelecom Basis";
  src: url("../fonts/rostelecom-basis-reg.woff2") format("woff2"), url("../fonts/rostelecom-basis-reg.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0 auto;
  padding: 0;
  min-width: 1024px;
  max-width: 1920px;
  height: 100%;
  font-family: "PT Sans", "Georgia", serif;
  color: #303030;
  background-color: #ffffff;
  display: flex;
  flex-direction: column; }

img {
  max-width: 100%;
  height: auto; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

button {
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main {
  flex-grow: 1; }

.swiper {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  font-family: "Rostelecom Basis", "Ðrial", sans-serif; }

.swiper__wrapper {
  display: flex;
  height: 100%; }

.swiper__slide {
  height: auto;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  user-select: text; }

.swiper__slide-3 .card__img {
  margin-left: -375px; }

.swiper__slide-4 .card__img {
  margin-left: -275px; }

.swiper__slide-1 {
  background-color: #80cbc4; }

.swiper__slide-2 {
  background-color: #e8b3ac; }

.swiper__slide-3 {
  background-color: #cacaca; }

.swiper__slide-4 {
  background-color: #9bd6e3; }

.swiper__button {
  width: 65px;
  height: 112px;
  position: absolute;
  top: 50%;
  margin-top: -56px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../img/icons/arrow.svg");
  transition: all 0.4s ease-in; }
  .swiper__button:focus, .swiper__button:active {
    outline: none; }

.swiper-button-animate:before {
  content: '';
  background-color: #ffffff;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: scale(0.001, 0.001);
  animation: swiper-button 0.8s ease-out; }

.swiper__button-prev {
  left: 72.7px; }

.swiper__button-next {
  transform: rotateY(180deg);
  right: 72.7px; }

.swiper__pagination {
  position: absolute;
  bottom: 10px;
  margin: 0;
  display: flex;
  justify-content: center;
  cursor: auto; }
  .swiper__pagination:focus, .swiper__pagination:active {
    border: none;
    outline: none; }

.swiper__pagination-bullet {
  margin-top: 6px;
  width: 12px;
  height: 12px;
  display: inline-block;
  border: 1px solid #ffffff;
  cursor: pointer;
  margin-left: 24px; }
  .swiper__pagination-bullet:first-child {
    margin-left: 0; }

.swiper__pagination-bullet-active {
  margin-top: 0;
  width: 24px;
  height: 24px; }

.swiper-horizontal > .swiper__pagination-bullets {
  width: 100%; }

@keyframes swiper-button {
  50% {
    transform: scale(1.5, 1.5);
    opacity: 0; }
  99% {
    transform: scale(0.001, 0.001);
    opacity: 0; }
  100% {
    transform: scale(0.001, 0.001);
    opacity: 1; } }

.header__wrapper {
  display: flex;
  padding-left: 232px; }

.header__logo-link {
  margin-top: 15px;
  width: 192px;
  height: 82px; }
  .header__logo-link:focus, .header__logo-link:active {
    outline: 1px solid #303030;
    outline-offset: 3px; }

.header__content {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-left: 120px; }

.bottom {
  padding-top: 11px;
  padding-bottom: 13px;
  flex-grow: 1; }

.bottom__list {
  display: flex;
  flex-wrap: wrap; }

.bottom__item {
  margin-right: 44px;
  width: min-content; }
  .bottom__item:last-child {
    margin: 0;
    width: 215px; }

.bottom__item-link {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 16px;
  color: #003883;
  font-weight: 700;
  display: block; }
  .bottom__item-link:focus, .bottom__item-link:active {
    outline: 1px solid #303030;
    outline-offset: 3px; }

.card {
  display: flex;
  justify-content: space-between;
  position: relative;
  height: 100%;
  width: 100%;
  color: #303030;
  padding-left: 260px;
  padding-right: 130px; }

.card__img {
  position: absolute;
  left: 50%;
  bottom: 0;
  margin-left: -240px; }

.card__left {
  width: 340px;
  padding-top: 88px; }

.card__left-title {
  margin: 0;
  padding: 0;
  font-size: 35px;
  line-height: 43px;
  text-transform: uppercase;
  margin-bottom: 30px; }

.card__left-text {
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 26px; }

.card__right {
  width: 395px;
  padding-top: 193px; }

.card__right-value {
  margin: 0;
  padding: 0;
  font-size: 120px;
  line-height: 120px; }

.card__right-sum {
  margin: 0;
  padding: 0;
  font-size: 48px;
  line-height: 62px; }

.card__right-text {
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 26px;
  width: 340px;
  margin-left: 15px; }

.top {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(0, 56, 131, 0.4); }

.top__left {
  padding-top: 7px; }

.top__left-title {
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 24px;
  color: #0061a1; }

.top__right {
  position: relative;
  min-width: 555px;
  padding-top: 9px;
  padding-left: 40px;
  padding-bottom: 5px;
  background: linear-gradient(270deg, #003883 0%, #0061A1 97.05%); }
  .top__right::before {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 0;
    height: 0;
    border-top: 0px solid transparent;
    border-left: 15px solid #ffffff;
    border-bottom: 40px solid transparent; }

.top__right-list {
  display: flex;
  align-items: center;
  height: 25px; }

.top__right-item {
  margin-left: 35px; }
  .top__right-item:first-child {
    margin: 0; }

.top__right-button {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 25px;
  color: #ffffff; }
  .top__right-button:focus, .top__right-button:active {
    outline: 1px solid #ffffff;
    outline-offset: 3px; }

.top__right-button--search {
  width: 22px;
  height: 21px;
  background-image: url("../img/icons/search.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain; }

.top__right-button--settings {
  width: 19px;
  height: 22px;
  background-image: url("../img/icons/settings.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain; }

.top__right-button--save,
.top__right-button--language {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 25px; }
